import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '@environments/environment';
import { User } from '@models';
import { IUser } from '@interfaces';

@Injectable({
  providedIn: 'root',
})
export class UsersService {
  constructor(public http: HttpClient) {}

  /**
   * Function that gets the users from the server.
   * @param restaurantId the id of the restaurant to look for its users.
   * @returns an observable with the array of users.
   */
  public getUsers(restaurantId: number): Observable<{ users: Array<User> }> {
    const url = environment.RESTAURANT_USERS.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.get<{ users: Array<User> }>(url);
  }

  /**
   * Function that creates a user with role of staff at the server .
   * @param restaurantId the id of the restaurant to assign the user.
   * @param staff the data of the user to create.
   * @returns an empty Observable.
   */
  public createStaff(restaurantId: number, staff: FormData): Observable<void> {
    const url = environment.RESTAURANT_USERS.replace(
      '{restaurantId}',
      restaurantId.toString()
    );
    return this.http.post<void>(url, staff);
  }

  /**
   * Function that edit the info of a user at the server.
   * @param userId the id of the user to edit.
   * @param user the data of the user to save at the server.
   * @returns an empty Observable.
   */
  public updateStaff(userId: number, user: FormData): Observable<void> {
    const url = environment.USERS_ID.replace('{userId}', userId.toString());
    return this.http.put<void>(url, user);
  }

  /**
   * Function that creates a user with role of manager at the server.
   * @param franchiseId the id of the franchise to assign the user.
   * @param manager the data of the user to create.
   * @returns an empty Observable.
   */
  public createManager(
    franchiseId: number,
    manager: FormData
  ): Observable<void> {
    const url = environment.FRANCHISES_MANAGERS.replace(
      '{franchiseId}',
      franchiseId.toString()
    );
    return this.http.post<void>(url, manager);
  }

  /**
   * Function that deletes a user at the server.
   * @param userId the id of the user to be deleted.
   * @returns an empty Observable.
   */
  public deleteUser(userId: number): Observable<void> {
    const url = environment.USERS_ID.replace('{userId}', userId.toString());
    return this.http.delete<void>(url);
  }

  /**
   * Get the profile of the current user.
   * @returns Observable with the user's prifile information.
   */
  public getUserDetails(): Observable<IUser> {
    return this.http.get<IUser>(environment.USERS_PROFILE);
  }

  /**
   * Update the user's profile information.
   * @param userData FormData with the new profile's information.
   * @returns Observable with the new profile's information.
   */
  public updateUserProfile(userData: FormData): Observable<IUser> {
    return this.http.put<IUser>(environment.USERS_PROFILE, userData);
  }
}
