import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '@environments/environment';
import { Observable } from 'rxjs';
import { StripeStatus } from '@interfaces';

@Injectable({
  providedIn: 'root',
})
export class PaymentService {
  constructor(private http: HttpClient) {}

  /**
   * Function that gets the stripe status from the server.
   */
  public getStripeStatus(): Observable<StripeStatus> {
    return this.http.get<StripeStatus>(environment.STRIPE_OAUTH_STATUS);
  }

  /**
   * Function that gets a link to go for the stripe configuration.
   */
  public getStripeLink(): Observable<string> {
    return this.http.get<string>(environment.STRIPE_LINK);
  }
}
