import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
  name: 'userRole',
})
export class UserRolePipe implements PipeTransform {
  constructor() {}
  transform(role: string): string {
    let spanishRole = '';
    switch (role) {
      case 'staff':
        spanishRole = 'Staff';
        break;
      case 'manager':
        spanishRole = 'Gerente';
        break;
      case 'admin':
        spanishRole = 'Administrador';
        break;
      default:
        spanishRole = 'No se ha podido identificar el rol';
    }
    return spanishRole;
  }
}
