import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import { Router } from '@angular/router';
import { FranchiseService, RestaurantService } from '@services';
import {
  ResponseFranchises,
  Franchise,
  ResponseRestaurant,
  Restaurant
} from '@models';
import * as firebase from 'firebase/app';
import { IRestaurant } from '@interfaces';
@Injectable({
  providedIn: 'root'
})
export class FranchiseAndRestaurantValidityGuard implements CanActivate {
  constructor(
    private franchiseService: FranchiseService,
    private restaurantService: RestaurantService,
    private router: Router
  ) {}
  async canActivate(): Promise<boolean> {
    const franchiseId = parseInt(
      JSON.parse(localStorage.getItem('franchiseId')),
      10
    );
    const restaurantId = parseInt(
      JSON.parse(localStorage.getItem('restaurantId')),
      10
    );
    if (!isNaN(franchiseId) && !isNaN(restaurantId)) {
      const franchisesResponse: ResponseFranchises = await this.franchiseService.getFranchisesAsPromise();
      const franchises = franchisesResponse.franchises;
      if (franchises.length > 0) {
        const franchiseIndex = this.isIdContainedByAnyOfTheFranchises(
          franchises,
          franchiseId
        );
        if (franchiseIndex !== -1) {
          const responseRestaurant: ResponseRestaurant = await this.restaurantService.getRestaurantsAsPromise(
            franchiseId
          );
          const restaurants = responseRestaurant.restaurants.map(
            (restaurant: IRestaurant) => {
              return new Restaurant(restaurant);
            }
          );
          if (restaurants.length > 0) {
            const restaurantIndex = this.isIdContainedByAnyOfTheRestaurants(
              restaurants,
              restaurantId
            );
            if (restaurantIndex !== -1) {
              this.franchiseService.setFranchise(franchises[franchiseIndex]);
              this.restaurantService.setRestaurant(
                restaurants[restaurantIndex]
              );
              return true;
            } else {
              this.idsDetectedAsInvalid();
              return false;
            }
          } else {
            this.idsDetectedAsInvalid();
            return false;
          }
        } else {
          this.idsDetectedAsInvalid();
          return false;
        }
      } else {
        this.idsDetectedAsInvalid();
        return false;
      }
    } else {
      this.idsDetectedAsInvalid();
      return false;
    }
  }

  private isIdContainedByAnyOfTheFranchises(
    franchises: Array<Franchise>,
    id: number
  ): number {
    for (let i = 0; i < franchises.length; i++) {
      if (franchises[i].id === id) {
        return i;
      }
    }
    return -1;
  }

  private isIdContainedByAnyOfTheRestaurants(
    restaurants: Array<Restaurant>,
    id: number
  ): number {
    for (let i = 0; i < restaurants.length; i++) {
      if (restaurants[i].id === id) {
        return i;
      }
    }
    return -1;
  }

  private idsDetectedAsInvalid(): void {
    localStorage.clear();
    firebase
      .auth()
      .signOut()
      .finally(() => {
        this.router.navigate(['']);
      });
  }
}
