import { Injectable } from '@angular/core';
import {
  CanActivate,
  ActivatedRouteSnapshot,
  RouterStateSnapshot,
  Router,
} from '@angular/router';
import * as firebase from 'firebase/app';

@Injectable({
  providedIn: 'root',
})
export class ClaimsGuard implements CanActivate {
  constructor(private router: Router) {}

  public canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Promise<boolean> {
    return firebase
      .auth()
      .currentUser.getIdTokenResult()
      .then(
        (idTokenResult) => {
          const hasUserAdminPrivileges = idTokenResult.claims.admin;
          const hasUserStaffPrivileges = idTokenResult.claims.staff;
          if (
            hasUserAdminPrivileges ||
            (hasUserStaffPrivileges && state.url.split('/')[1] === 'staff')
          ) {
            // If the user has admin privileges or staff and we are in the staff layout the route is correct.
            return true;
          } else if (hasUserStaffPrivileges) {
            // If the user has staff privileges but it's on the incorrect route we send it to the staff layout.
            this.router.navigate(['staff']);
            return false;
          } else {
            this.idsDetectedAsInvalid();
            return false;
          }
        },
        () => {
          this.idsDetectedAsInvalid();
          return false;
        }
      );
  }

  /**
   * If there is a trouble with the session it is tried to finish.
   */
  private idsDetectedAsInvalid(): void {
    localStorage.clear();
    firebase
      .auth()
      .signOut()
      .finally(() => {
        this.router.navigate(['']);
      });
  }
}
