import { Pipe, PipeTransform } from '@angular/core';
@Pipe({
  name: 'loginErrorCode',
})
export class LoginErrorCodePipe implements PipeTransform {
  constructor() {}
  transform(errorCode: string): string {
    let spanishError = '';
    switch (errorCode) {
      case 'auth/user-not-found':
        spanishError = 'El usuario no se encuentra registrado';
        break;
      case 'auth/invalid-email':
        spanishError = 'El correo ingresado es inválido';
        break;
      case 'auth/wrong-password':
        spanishError = 'La contraseña ingresada es incorrecta';
        break;
      case 'auth/not-an-admin':
        spanishError = 'La cuenta no cuenta con permisos de administrador';
        break;
      default:
        spanishError = 'No se ha podido identificar el error';
    }
    return spanishError;
  }
}
