import {
  Component,
  Input,
  OnChanges,
  Output,
  EventEmitter,
} from '@angular/core';

@Component({
  selector: 'app-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.scss'],
})
export class PaginationComponent implements OnChanges {
  /**
   * Total of pages to show.
   */
  @Input() public totalPages = 0;

  /**
   * Tells to the parent element that the selected page has benn changed,
   */
  @Output() public currentPage = new EventEmitter<number>();

  /**
   * actual selected page.
   */
  public actualPage = 1;

  constructor() {
    this.resetPagination();
  }

  ngOnChanges(): void {
    this.resetPagination();
  }

  /**
   * Set a new selected page.
   * @param page Number of the new page.
   */
  public changePage(page: number): void {
    if (page !== this.actualPage) {
      if (page > 0 && page <= this.totalPages) {
        this.currentPage.next(page);
        this.actualPage = page;
      }
    }
  }

  /**
   * Set the first page as selected.
   */
  public firstPage(): void {
    this.changePage(1);
  }

  /**
   * Set the last page as selected.
   */
  public lastPage(): void {
    this.changePage(this.totalPages);
  }

  /**
   * Set the default values of the global variables.
   */
  public resetPagination(): void {
    this.actualPage = 1;
    this.currentPage.next(this.actualPage);
  }
}
